import React, {Component} from 'react';
import ReactDOM from 'react-dom';

class Test extends Component {

    constructor(props) {
        super(props);
    }
    render(){
        return (<div>
            {''}
        </div>)
    }
}
class Main extends Component {

    constructor(props) {
        super(props);

        this.state = {
            list: [],
            showSetting: false
        };

        this.getList()
            .then(({data}) => {
                this.setState({
                    list: data
                });
            });
    }

    getList() {
        return fetch('http://localhost:8099/list')
            .then(res => res.json());
    }

    render() {
        console.log(Test)
        return (<div className="container1">
            {''}
            <Test></Test>
        </div>);
    }

    skip() {
        console.log('!');
    }

}

ReactDOM.render(
    <Main />,
    document.getElementById('app')
);
